// eslint-disable-next-lin

import React, { useState } from 'react';
import './MENU.css';
import { GiCastle } from "react-icons/gi";
import { GiHamburgerMenu } from "react-icons/gi";





function MENU(){ 

    const [showMenu, setshowMenu ] = useState();
    
    return(
        <nav className = "navbar">
            <div className = "navbar_logo">
            <GiCastle/>
            <a href = ""> Kingdom Calculator</a>
            </div>

            <ul className = "navbar_menu" id ={showMenu ? "hidden" : ""}>
                <li><a href="">스페셜 쿠키 뽑기확률</a></li>
                <li><a href="">고급 쿠기 뽑기확률</a></li>
                <li><a href="">보물 뽑기확률</a></li>
                <li><a href="">길드 뽑기확률</a></li>
                <li><a href="">쿠폰입력</a></li>
            </ul>

            <a href = "" className = "navbar_toogle" onClick = {() => setshowMenu(!showMenu)}>
                <GiHamburgerMenu/> 
            </a>
        </nav>
        
        );

    }

    

export default MENU;